<template>
  <div class="ideas main-wrap">
    <h2 class="nav">{{header}}</h2>
    <div class="container">
      <!-- 输入区 -->
      <div class="form-wrap">
        <div class="sticky">
          <div class="inp-title">
            <input
              type="text"
              v-model="title"
              placeholder="标题..."
              :class="focus_title?'border-main':''"
              @focus="fn_focus_title()"
              @blur="fn_blur_title()"
            />
          </div>
          <div class="inp-idea">
            <textarea
              placeholder="添加项目..."
              ref="textarea"
              class="g333"
              :class="focus_textarea?'border-main':''"
              cols="30"
              rows="10"
              v-model="idea"
              maxlength="100"
              @focus="fn_focus_text()"
              @blur="fn_blur_text()"
              @keyup.ctrl.enter="fn_add()"
            ></textarea>
          </div>
          <div class="submit" @click="fn_add()">
            <div class="border"></div>
            <div class="content">添加</div>
          </div>
        </div>
      </div>
      <!-- 功能块 -->
      <div class="fn-main" id="fn-main" :style="fn_main?{height:'200px'}:{}">
        <span class="iconfont" @mousedown="$drag($event)">&#xe70c;</span>
        <input type="text" v-model="idea" @focus="fn_main_show()" @blur="fn_main_show()" />
      </div>
      <!-- 展示区 -->
      <ul class="idea-list">
        <li
          v-for="(item,index) in dataList"
          :key="index"
          v-show="dataList"
          class="idea-cell"
          :style="dataList[index].show?{height:'20px'}:{}"
        >
          <p class="date">
            <span class="pack-up" @click="fn_pickUp(index)">{{dataList[index].show?'展开':'收起'}}</span>
            <span class="fn-pack">
              <span :class="fn_state(index)">{{index}}</span>
              <i>{{dataList[index].show?`-共${item.length}则-`:''}}</i>
            </span>
          </p>
          <div v-for="(item2,index2) in item" :key="index2" class="c-ideas clearfix">
            <span class="info f-l" :class="item2.done?'done':''">{{index2+1}}、{{item2.idea}}</span>
            <div class="fn f-r">
              <div class="complete" @click="fn_complete({date:index,index2:index2})">
                <div class="border" :class="item2.done?' complete-border':''"></div>
                <div class="content" :class="item2.done?' complete-content':''"></div>
              </div>
              <div class="delete" @click="fn_delete({date:index,index2:index2})">删除</div>
            </div>
          </div>
        </li>
        <li class="hide" v-show="JSON.stringify(this.dataList)=='{}'">
          <p>还没有待办事项~</p>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  name: "ideas",
  data() {
    return {
      header: "待办事项",
      idea: undefined,
      date: undefined,
      title: "",
      completed: false,
      focus_textarea: false,
      focus_title: false,
      switch: false,
      fn_main: false
    };
  },
  //
  methods: {
    fn_add() {
      if (this.idea) {
        this.$store.commit("fn_add", {
          action: "ideas",
          idea: this.idea,
          // 如果想要类似博客的效果，可以将事件改为精确到时、分、秒，这样每个对象的名字就不一样了
          date: `${new Date().getFullYear()}-${new Date().getMonth() +
            1}-${new Date().getDate()}`
        });
        if (this.had) {
          alert("已存在的项目");
        }
      } else {
        alert("待办项目数据不完整,无法添加!");
      }
      this.idea = this.date = undefined;
    },
    fn_complete(params) {
      this.$store.commit(
        "fn_complete",
        Object.assign(params, { action: "ideas" })
      );
    },
    fn_delete(params) {
      if (confirm("请注意，即将删除项目！！！")) {
        this.$store.commit(
          "fn_delete",
          Object.assign(params, { action: "ideas" })
        );
      }
    },
    fn_focus_title() {
      console.log(1);
      this.focus_title = true;
    },
    fn_blur_title() {
      console.log(2);
      this.focus_title = false;
    },
    fn_focus_text() {
      this.focus_textarea = true;
    },
    fn_blur_text() {
      this.focus_textarea = false;
    },
    fn_pickUp(index) {
      this.$set(this.dataList[index], "show", !this.dataList[index].show);
    },
    fn_state(item) {
      if (
        new Date(item).getMonth() == new Date().getMonth() &&
        new Date(item).getDate() == new Date().getDate() &&
        new Date(item).getFullYear() == new Date().getFullYear()
      ) {
        return "now";
      } else if (
        (new Date(item).getMonth() == new Date().getMonth() &&
          new Date(item).getDate() < new Date().getDate() &&
          new Date(item).getFullYear() == new Date().getFullYear()) ||
        (new Date(item).getMonth() < new Date().getMonth() &&
          new Date(item).getFullYear() == new Date().getFullYear())
      ) {
        return "before";
      } else {
        return "future";
      }
    },
    fn_main_show() {
      this.fn_main = !this.fn_main;
    }
  },
  computed: {
    dataList() {
      return this.$store.getters.ret_ideas;
    },
    had() {
      return this.$store.getters.ret_has_idea;
    }
  }
};
</script>

<style scoped>
@import "../assets/style/ideas.css";
</style>



