<style>
* {
  padding: 0;
  margin: 0;
  list-style: none;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}
body {
  background-color: rgb(245, 245, 245);
}
@font-face {
  font-family: "iconfont"; /* project id 1676031 */
  src: url("https://at.alicdn.com/t/font_1676031_f66ofozivbq.eot");
  src: url("https://at.alicdn.com/t/font_1676031_f66ofozivbq.eot?#iefix")
