<template>
  <div class="todos main-wrap">
    <h2 class="nav">{{header}}</h2>
    <div class="container">
      <div class="form-wrap">
        <div class="sticky">
          <div class="inp-todo">
            <textarea
              placeholder="添加项目..."
              ref="textarea"
              class="g333"
              :class="focus_textarea?'border-main':''"
              cols="30"
              rows="10"
              v-model="todo"
              maxlength="100"
              @focus="fn_focus()"
              @blur="fn_blur()"
            ></textarea>
          </div>
          <div class="inp-date">
            <input type="date" v-model="date" />
          </div>
          <div class="submit" @click="fn_add()">
            <div class="border"></div>
            <div class="content">添加待办</div>
          </div>
        </div>
      </div>
      <!-- 功能块 -->
      <div class="fn-main" id="fn-main" :style="fn_main?{height:'200px'}:{}">
        <span class="iconfont" @mousedown="$drag($event)">&#xe70c;</span>
        <input type="text" v-model="todo" @focus="fn_main_show()" @blur="fn_main_show()" />
      </div>
      <!-- 展示区 -->
      <ul class="todo-list">
        <li
          v-for="(item,index) in dataList"
          :key="index"
          v-show="dataList"
          class="todo-cell"
          :style="dataList[index].show?{height:'20px'}:{}"
        >
          <p class="date">
            <span class="pack-up" @click="fn_pickUp(index)">{{dataList[index].show?'展开':'收起'}}</span>
            <span class="fn-pack">
              <span :class="fn_state(index)">{{index}}</span>
              <i>{{dataList[index].show?`-共${item.length}则-`:''}}</i>
            </span>
          </p>
          <div v-for="(item2,index2) in item" :key="index2" class="c-todos clearfix">
            <li :class="item2.done?'done':''" class="info f-l">{{index2+1}}、{{item2.todo}}</li>
            <div class="fn f-r">
              <div class="complete" @click="fn_complete({date:index,index2:index2})">
                <div class="border" :class="item2.done?' complete-border':''"></div>
                <div class="content" :class="item2.done?' complete-content':''"></div>
              </div>
              <div class="delete" @click="fn_delete({date:index,index2:index2})">删除</div>
            </div>
          </div>
        </li>
        <li class="hide" v-show="JSON.stringify(this.dataList)=='{}'">
          <p>还没有待办事项~</p>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  name: "todos",
  data() {
    return {
      header: "待办事项",
      todo: undefined,
      date: undefined,
      completed: false,
      focus_textarea: false,
      switch: false,
      fn_main: false
    };
  },
  //
  methods: {
    fn_add() {
      if (this.todo && this.date) {
        this.$store.commit("fn_add", {
          action: "todos",
          todo: this.todo,
          date: this.date,
          done: false
        });
        if (this.had) {
          alert("已存在的项目");
        }
      } else {
        alert("待办项目数据不完整,无法添加!");
      }
      this.todo = this.date = undefined; //添加此行后实现了computed属性与视图属性同步，猜测是改变todo和date的值引起dataList依赖的属性值发生改变，另：computed里面的属性依赖data里面的属性的值?
      // console.log(this.dataList);
    },
    fn_complete(params) {
      this.$store.commit(
        "fn_complete",
        Object.assign(params, { action: "todos" })
      );
      // this.todo = this.date = undefined;
    },
    fn_delete(params) {
      if (confirm("请注意，即将删除项目！！！")) {
        this.$store.commit(
          "fn_delete",
          Object.assign(params, { action: "todos" })
        );
      }
      // this.todo = this.date = undefined;
    },
    fn_focus() {
      this.focus_textarea = true;
    },
    fn_blur() {
      this.focus_textarea = false;
    },
    fn_pickUp(index) {
      // 这是传入事件，通过事件委托的方式事件收起，但是高度好像没有办法过渡
      // if (
      //   getComputedStyle(e.target.parentNode.parentNode)["height"] == "20px"
      // ) {
      //   e.target.parentNode.parentNode.style.height = "auto";
      //   e.target.parentNode.parentNode.style.opacity = "1";
      // } else {
      //   e.target.parentNode.parentNode.style.height = "20px";
      //   e.target.parentNode.parentNode.style.opacity = ".1";
      // }
      this.$set(this.dataList[index], "show", !this.dataList[index].show);
      //上述方法已经解决了问题...
      // this.dataList[index].show = !this.dataList[index].show;
      // console.log(this.dataList[index].show, "------");
      //数据改变了，视图未刷新，点击输入框后改变了data内数据，测试页面刷新了
      // this.switch = !this.switch;
      // console.log(this.switch)
      //加一个开关，每次点击收起都会导致开关的值改变，从而引起data内容改变,希望间接实现了收起功能...
      //测试后发现不行，但是改变this.focus_textarea值却可以，猜测是this.focus_textarea会导致页面页面重渲染，间接...
    },
    fn_state(item) {
      if (
        new Date(item).getMonth() == new Date().getMonth() &&
        new Date(item).getDate() == new Date().getDate() &&
        new Date(item).getFullYear() == new Date().getFullYear()
      ) {
        return "now";
      } else if (
        (new Date(item).getMonth() == new Date().getMonth() &&
          new Date(item).getDate() < new Date().getDate() &&
          new Date(item).getFullYear() == new Date().getFullYear()) ||
        (new Date(item).getMonth() < new Date().getMonth() &&
          new Date(item).getFullYear() == new Date().getFullYear())
      ) {
        return "before";
      } else {
        return "future";
      }
    },
    fn_main_show() {
      this.fn_main = !this.fn_main;
    }
  },
  //计算属性,是依赖data中的数据的
  computed: {
    dataList() {
      return this.$store.getters.ret_todos;
    },
    had() {
      return this.$store.getters.ret_has_todo;
    }
  },
  //bfcreat和created有区别的!!!

  created() {}
};
</script>

<style scoped>
@import "../assets/style/todos.css";
</style>



