// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from "vue";
import App from "./App";
import router from "./router"; //router组件居然不用首字母大写！
import axios from "axios";
import store from "./store";

Vue.prototype.$axios = axios;
Vue.config.productionTip = false;
//可拖动
const drag = function(ev) {
  var oDiv = ev.target.parentNode;
  var disX = ev.clientX - oDiv.offsetLeft;
  var disY = ev.clientY - oDiv.offsetTop;
  document.onmousemove = function(ev) {
    var l = ev.clientX - disX;
    var t = ev.clientY - disY;
    oDiv.style.left = l + "px";
    oDiv.style.top = t + "px";
  };
  document.onmouseup = function() {
    document.onmousemove = null;
    document.onmouseup = null;
  };
  // console.log(this.$refs.textarea.offsetLeft,this.$refs.textarea.clientWidth)
  // console.dir(this.$refs.textarea)
  //想试试表达这个可动块在textaarea里面的状态的
};
Vue.prototype.$drag = drag;
/* eslint-disable no-new */
new Vue({
  el: "#app",
  router,
  store,
  components: { App },
  template: "<App/>"
});



