import Vue from "vue";
import Vuex from "vuex";
//
Vue.use(Vuex);
export default new Vuex.Store({
  state: {
    todos: JSON.parse(localStorage.getItem("todos") || "{}"),
    ideas: JSON.parse(localStorage.getItem("ideas") || "{}"),
    has_todo: false,
    has_idea: false
  },
  //组件获取store属性
  getters: {
    ret_todos(state) {
      return state.todos;
    },
    ret_has_todo(state) {
      return state.has_todo;
    },
    ret_ideas(state) {
      return state.ideas;
    },
    ret_has_idea(state) {
      return state.has_idea;
    }
  },
  //组件传入、改变state属性
  mutations: {
    //添加项目
    fn_add(state, payload) {
      switch (payload.action) {
        //todos
        case "todos":
          //存在该日期
          if (state.todos[payload.date]) {
            //该日期下没有改项目
            if (
              state.todos[payload.date].filter(item => {
                return item === payload.todo;
              }).length === 0
            ) {
              state.todos[payload.date].push({
                todo: payload.todo,
                done: payload.done
              });
            }
            //该项目下有该项目
            else {
              state.has_todo = true;
              setTimeout(() => {
                state.has_todo = false;
              }, 10);
            }
          }
          //不存在该日期
          else {
            let tempObj = state.todos;
            tempObj[payload.date] = [];
            tempObj[payload.date].push({
              todo: payload.todo,
              done: payload.done
            });
            state.todos = tempObj;
          }
          //排序
          var newData = {};
          Object.keys(state.todos)
            .sort()
            .forEach(function(key) {
              newData[key] = state.todos[key];
            });
          state.todos = newData;
          //存值
          localStorage.setItem("todos", JSON.stringify(state.todos));
          break;
        //ideas
        case "ideas":
          //存在该日期
          if (state.ideas[payload.date]) {
            //该日期下没有改项目
            if (
              state.ideas[payload.date].filter(item => {
                return item === payload.idea;
              }).length === 0
            ) {
              state.ideas[payload.date].push({
                idea: payload.idea,
                done: payload.done
              });
            }
            //该项目下有该项目
            else {
              state.has_idea = true;
              setTimeout(() => {
                state.has_idea = false;
              }, 10);
            }
          }
          //不存在该日期
          else {
            let tempObj = state.ideas;
            tempObj[payload.date] = [];
            tempObj[payload.date].push({
              idea: payload.idea,
              done: payload.done
            });
            state.ideas = tempObj;
          }
          //排序
          var newData = {};
          Object.keys(state.ideas)
            .sort()
            .forEach(function(key) {
              newData[key] = state.ideas[key];
            });
          state.ideas = newData;
          //存值
          localStorage.setItem("ideas", JSON.stringify(state.ideas));
          break;
        default:
          null;
          break;
      }
    },
    //完成项目
    fn_complete(state, payload) {
      switch (payload.action) {
        //todos
        case "todos":
          state.todos[payload.date][payload.index2].done = !state.todos[
            payload.date
          ][payload.index2].done;
          localStorage.setItem("todos", JSON.stringify(state.todos));
          break;
        //ideas
        case "ideas":
          state.ideas[payload.date][payload.index2].done = !state.ideas[
            payload.date
          ][payload.index2].done;
          localStorage.setItem("ideas", JSON.stringify(state.ideas));
        default:
          break;
      }
    },
    //删除项目
    fn_delete(state, payload) {
      switch (payload.action) {
        //todos
        case "todos":
          state.todos[payload.date].splice(payload.index2, 1);
          if (state.todos[payload.date].length == 0) {
            delete state.todos[payload.date];
          }
          localStorage.setItem("todos", JSON.stringify(state.todos));
          break;
        //ideas
        case "ideas":
          state.ideas[payload.date].splice(payload.index2, 1);
          if (state.ideas[payload.date].length == 0) {
            delete state.ideas[payload.date];
          }
          localStorage.setItem("ideas", JSON.stringify(state.ideas));
        default:
          break;
      }
    }
  }
});



